.product{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  max-height: 400px;
  width: 100%;
  min-width: 100px;
  background-color: white;
  z-index: 1;
}
.product > img{
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}
.product > button{
  background: #f0d14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}

.product > .product_price{
  margin-top: 5px;
}

.product_info{
  height: 100px;
  margin-bottom: 15px;
}

.product_rating{
  display: flex;
}